@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}

.gameBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 8;
  width: 100%;
  min-height: 512px;
  overflow: hidden;

  background: use(--template-gameBlock-background);
  box-shadow: 0px 12px 7px rgba(0, 0, 0, 0.1);
}

.gameWrapper {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 spacing(3);
  background: use(--template-gameCell-wrapper);
}

.game {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  max-width: 600px;
  min-height: 520px;

  @media (max-width: 600px) {
    min-height: 82vw;
  }

  & > canvas {
    display: flex;
    pointer-events: none;
  }

  & > .fps {
    position: absolute;
    left: 50px;
    top: 50px;
    color: #ffffff;
    z-index: 10px;
  }
}

.gameBlockBackground {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.26;
  background: #000000;
  box-shadow: inset 0 1px 100px 0 rgba(0, 0, 0, 0.5);
}

.statisticsWrapper {
  padding-top: 26px;
}

.gameControllerWrapper {
  position: relative;
  width: 100%;
  background: use(--template-gameCell-controller);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 500;

  & input {
    font-weight: 500;
  }
}
